import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Checkbox, Table, Col, Select, DatePicker } from 'antd';
import { teachers } from 'dump/data';

const { Option } = Select;

const columns = [
  {
    title: 'Đã trả',
    dataIndex: 'sent',
    render: text => <a href="#">{text}</a>,
  },
  {
    title: 'Các loại giấy tờ sinh viên nộp',
    dataIndex: 'typeDoc',
  },
  {
    title: 'Ngày trả',
    dataIndex: 'dateSent',
  },
  {
    title: 'Ghi chú',
    dataIndex: 'note',
  },
];
const data = [
  {
    key: '1',
    typeDoc: 'Phiếu báo thay đổi hộ khẩu',
    dateSent: '05/05/2005',
    note: '',
  },
  {
    key: '2',
    typeDoc: 'Giấy chuyển sinh hoạt Đoàn Đảng',
    dateSent: '05/05/2005',
    note: '',
  },
  {
    key: '3',
    typeDoc: 'Giấy chuyển nghĩa vụ quân sự',
    dateSent: '05/05/2005',
    note: '',
  },
];

// rowSelection object indicates the need for row selection
const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
  },
  getCheckboxProps: record => ({
    disabled: record.name === 'Disabled User', // Column configuration not to be checked
    name: record.name,
  }),
};

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    birthdate: '',
    academicRank: '',
    degree: '',
    title: '',
    position: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormReturnDoc extends Component {
  state = {
    teacherId: true,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = code => {
    const teacherMap = teachers.filter(teacher => teacher.code === code)[0];
    const initialValue = handleInitialValue(teacherMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Card title={teacherId ? 'Trả giấy tờ' : 'Trả giấy tờ'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Row>
              <Col span={12}>
                <Form.Item label="Chọn tất cả" {...formItemLayout}>
                  {getFieldDecorator('allCheck', {
                    initialValue: initialValue.allCheck,
                  })(<Checkbox />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Ngày trả" {...formItemLayout}>
                  {getFieldDecorator('daySent', {
                    initialValue: initialValue.daySent,
                  })(<DatePicker />)}
                </Form.Item>
              </Col>
            </Row>
            <Table rowSelection={rowSelection} columns={columns} dataSource={data} />
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#return-doc/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormReturnDoc {...props} />
  </Page>
));



